@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Fira Sans',sans-serif;
  -webkit-font-smoothing: antialiased;
  font-weight: 400;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3 {
  font-weight: 500;
}

.form-block {
  max-width: 613px;
  margin-top: 42px;
  margin-left: 110px;
}

.view-mode  .ant-form-item-extra {
  display: none;
}

.ant-radio-button-wrapper {
  margin-right: 5px;
  border-radius: 5px;
}

.ant-radio-button-wrapper:last-child {
  border-radius: 5px;
}


@media (max-width: 768px) {
  .view-mode {
    display: none;
  }

  .form-block {
    margin-left: 42px;
  }
}

p {
  margin: 0;
  padding: 0;
}

.ant-input-suffix {
  color: #00000080;
}

.ant-input, .ant-input-affix-wrapper {
  border-radius: 5px;
}
.card-radio-wrapper {
    border: 1px solid #e4e4e4;
    border-radius: 10px;
    padding: 10px;
    flex: 1 1;

}

.card-radio-description {
    color: #00000080;
}

.ant-radio-wrapper-checked.card-radio-wrapper {
    border: 1px solid #48B5FE;
}

.ant-radio-wrapper.card-radio-wrapper:last-child {
    margin-right: 0;
}
.color-radio,
.color-radio + .ant-radio-button-wrapper:last-child,
.color-radio + .ant-radio-button-wrapper:first-child {
    width: 45px !important;
    height: 45px !important;
    border-radius: 100% !important;
    margin-right: 15px;
    border-left: transparent i !important;
}

.color-radio.ant-radio-button-wrapper-checked {
    background-color: transparent !important;
}

.color-radio + .ant-radio-button-wrapper:not(:first-child)::before {
    display: none;
}

